;white-space:nowrap;text-overflow:ellipsis;overflow:hidden;white-space:nowrap;transition:color .15s}.list__right{min-width:5rem;margin-left:1.3rem;text-align:right}.list__delete{position:relative;width:1rem;height:1rem;padding:.4rem;padding-right:0;box-sizing:content-box;margin-left:auto;margin-bottom:.3rem}.list__delete:before,.list__delete:after{top:.2rem;right:.4rem;position:absolute;display:block;content:"";width:.1rem;height:1.2rem;background-color:#b8b8b8;transition:background-color .15s}.list__delete:before{transform:rotate(-40deg)}.list__delete:after{transform:rotate(40deg)}.list__delete:hover:before,.list__delete:hover:after{background-color:#343434}.list__date{font-size:1.1rem;color:#939393;letter-spacing:.02rem}.list-enter{opacity:0;transform:translateX(-30px)}.list-enter-active,.list-leave-active{transition:transform .5s,opacity .5s}.list-leave-to{opacity:0;transform:translateX(30px)}