.main-input{position:relative}.main-input input{display:block;width:100%;font-size:1.5rem;box-sizing:border-box;padding:0 4.2rem 0 2.5rem;height:5.2rem;border-radius:30px;background-color:rgba(255,255,255,.48);box-shadow:0 15px 45px 0 rgba(0,0,0,.27);border:0;outline:0;transition:background-color .3s;cursor:text}.main-input input::-webkit-input-placeholder{letter-spacing:.04rem;font-family:"Muli",sans-serif;color:rgba(255,255,255,.8)}.main-input input:-moz-placeholder{letter-spacing:.04rem;font-family:"Muli",sans-serif;color:rgba(255,255,255,.8)}.main-input input::-moz-placeholder{letter-spacing:.04rem;font-family:"Muli",sans-serif;color:rgba(255,255,255,.8)}.main-input input:-ms-input-placeholder{letter-spacing:.04rem;font-family:"Muli",sans-serif;color:rgba(255,255,255,.8)}.main-input input:focus{background-color:rgba(255,255,255,.64)}.main-input input:focus+button{background-image:url('data:image/svg+xml;utf8,<svg version="1.1" id="Layer_1" xmlns="http://www.w3.org/2000/svg" x="0" y="0" viewBox="0 0 18 18" xml:space="preserve"><g id="Group_11" transform="translate(-308.8 -357.51)"><path id="Line_1" class="st0" d="M323.8 366.01h-12.5" fill="none" stroke="%23333333" stroke-width="1.6" stroke-linecap="round" stroke-linejoin="round"/><path id="Line_2" class="st0" d="M318.8 361.01l5 5" fill="none" stroke="%23333333" stroke-width="1.6" stroke-linecap="round" stroke-linejoin="round"/><path id="Line_3" class="st0" d="M318.8 371.01l5-5" fill="none" stroke="%23333333" stroke-width="1.6" stroke-linecap="round" stroke-linejoin="round"/></g></svg>')}.main-input button{position:absolute;top:50%;right:1.2rem;transform:translateY(-50%);width:3.3rem;height:3.3rem;background-color:#fafafa;border-radius:50%;background-size:1.62rem 1.62rem;background-position:50% 50%;background-repeat:no-repeat;background-image:url('data:image/svg+xml;utf8,<svg version="1.1" id="Layer_1" xmlns="http://www.w3.org/2000/svg" x="0" y="0" viewBox="0 0 18 18" xml:space="preserve"><g id="Group_11" transform="translate(-308.8 -357.51)"><path id="Line_1" class="st0" d="M323.8 366.01h-12.5" fill="none" stroke="%23717171" stroke-width="1.6" stroke-linecap="round" stroke-linejoin="round"/><path id="Line_2" class="st0" d="M318.8 361.01l5 5" fill="none" stroke="%23717171" stroke-width="1.6" stroke-linecap="round" stroke-linejoin="round"/><path id="Line_3" class="st0" d="M318.8 371.01l5-5" fill="none" stroke="%23717171" stroke-width="1.6" stroke-linecap="round" stroke-linejoin="round"/></g></svg>')}.main-input button:hover{background-image:url('data:image/svg+xml;utf8,<svg version="1.1" id="Layer_1" xmlns="http://www.w3.org/2000/svg" x="0" y="0" viewBox="0 0 18 18" xml:space="preserve"><g id="Group_11" transform="translate(-308.8 -357.51)"><path id="Line_1" class="st0" d="M323.8 366.01h-12.5" fill="none" stroke="%23333333" stroke-width="1.6" stroke-linecap="round" stroke-linejoin="round"/><path id="Line_2" class="st0" d="M318.8 361.01l5 5" fill="none" stroke="%23333333" stroke-width="1.6" stroke-linecap="round" stroke-linejoin="round"/><path id="Line_3" class="st0" d="M318.8 371.01l5-5" fill="none" stroke="%23333333" stroke-width="1.6" stroke-linecap="round" stroke-linejoin="round"/></g></svg>')}@-webkit-keyframes fadeShow{0%{opacity:0;transform:translate3d(0, 10px, 0)}100%{opacity:1;transform:translate3d(0, 0, 0)}}@-moz-keyframes fadeShow{0%{opacity:0;transform:translate3d(0, 10px, 0)}100%{opacity:1;transform:translate3d(0, 0, 0)}}@keyframes fadeShow{0%{opacity:0;transform:translate3d(0, 10px, 0)}100%{opacity:1;transform:translate3d(0, 0, 0)}}@-webkit-keyframes randomBackground{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}@-moz-keyframes randomBackground{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}@keyframes randomBackground{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}.modal__dim{display:-webkit-box;display:-moz-box;display:-ms-flexbox;display:-webkit-flex;display:flex;-webkit-justify-content:center;-moz-justify-content:center;-ms-justify-content:center;justify-content:center;-ms-flex-pack:center;-webkit-align-items:center;-moz-align-items:center;-ms-align-items:center;align-items:center;position:fixed;z-index:999;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,.4)}.modal__content{min-width:34rem;margin:0px auto;padding:3rem;background-color:#fff;border-radius:2px;box-shadow:0 10px 30px -4px rgba(10,10,10,.2);transition:all .3s ease}.modal__text{text-align:center;font-size:1.4rem;color:#4a4a4a}.modal__text:before{display:block;margin:0 auto;text-align:center;line-height:5rem;font-size:2.2rem;font-weight:bold;content:"알림!"}.modal__close{text-align:center;margin:2.5rem auto 0;padding:.4rem 1rem;border:1px solid #b1b1b1;border-radius:.2rem;font-size:1.2rem;color:#555}.modal__close:hover{color:#323232;border-color:#4a4a4a}.modal-enter{opacity:0}.modal-enter .modal__content{transform:translateY(30px);transition:transform .6s}.modal-enter-active{transition:opacity .3s}.modal-leave-active{transition:opacity .3s .05s}.modal-leave-to{opacity:0}.modal-leave-to .modal__content{transform:translateY(30px);transition:transform .6s}